import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup,
  Div,
  Radio,
} from "@vkontakte/vkui";

const ModalInfoVIP = ({ setActiveModal, openPaymentVIP, type, appAvatar }) => {
  return (
    <>
      {type === "Бронзовый" ? (
        <ModalRoot activeModal="modal-info-vip">
          <ModalCard
            id="modal-info-vip"
            onClose={() => setActiveModal(null)}
            icon={<Avatar size={72} src={appAvatar} />}
            header="Что дает Бронзовый аккаунт"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    1. Прибыль в 20% с каждого пополнения рефералов.
                  </InfoRow>
                  <InfoRow>
                    2. Вывод прибыли, когда на балансе не меньше 50 руб.
                  </InfoRow>
                  <InfoRow style={{ marginTop: "10px" }}>
                    К оплате: 9,00 руб.
                  </InfoRow>
                </Div>
              </>
            }
            actions={[
              {
                title: "Активировать",
                mode: "commerce",
                action: () => {
                  openPaymentVIP("Бронзовый");
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : type === "Серебряный" ? (
        <ModalRoot activeModal="modal-info-vip">
          <ModalCard
            id="modal-info-vip"
            onClose={() => setActiveModal(null)}
            icon={<Avatar size={72} src={appAvatar} />}
            header="Что дает Серебряный аккаунт"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    1. Прибыль в 30% с каждого пополнения рефералов.
                  </InfoRow>
                  <InfoRow>
                    2. Вывод прибыли, когда на балансе не меньше 40 руб.
                  </InfoRow>
                  <InfoRow style={{ marginTop: "10px" }}>
                    К оплате: 19,00 руб.
                  </InfoRow>
                </Div>
              </>
            }
            actions={[
              {
                title: "Активировать",
                mode: "commerce",
                action: () => {
                  openPaymentVIP("Серебряный");
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : type === "Золотой" ? (
        <ModalRoot activeModal="modal-info-vip">
          <ModalCard
            id="modal-info-vip"
            onClose={() => setActiveModal(null)}
            icon={<Avatar size={72} src={appAvatar} />}
            header="Что дает Золотой аккаунт"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    1. Прибыль в 40% с каждого пополнения рефералов.
                  </InfoRow>
                  <InfoRow>
                    2. Вывод прибыли, когда на балансе не меньше 30 руб.
                  </InfoRow>
                  <InfoRow style={{ marginTop: "10px" }}>
                    К оплате: 39,00 руб.
                  </InfoRow>
                </Div>
              </>
            }
            actions={[
              {
                title: "Активировать",
                mode: "commerce",
                action: () => {
                  openPaymentVIP("Золотой");
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : type === "Платиновый" ? (
        <ModalRoot activeModal="modal-info-vip">
          <ModalCard
            id="modal-info-vip"
            onClose={() => setActiveModal(null)}
            icon={<Avatar size={72} src={appAvatar} />}
            header="Что дает Платиновый аккаунт"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    1. Прибыль в 60% с каждого пополнения рефералов.
                  </InfoRow>
                  <InfoRow>
                    2. Вывод прибыли, когда на балансе не меньше 10 руб.
                  </InfoRow>
                  <InfoRow style={{ marginTop: "10px" }}>
                    К оплате: 69,00 руб.
                  </InfoRow>
                </Div>
              </>
            }
            actions={[
              {
                title: "Активировать",
                mode: "commerce",
                action: () => {
                  openPaymentVIP("Платиновый");
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : (
        ""
      )}
    </>
  );
};

export { ModalInfoVIP };
