import React, { useState, useEffect, useReducer } from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  Panel,
  PanelHeaderSimple,
  Button,
  Div,
  ActionSheet,
  ActionSheetItem,
  Slider,
  Select,
} from "@vkontakte/vkui";
import Icon16CheckCircle from "@vkontakte/icons/dist/16/check_circle";
import Icon16Smile from "@vkontakte/icons/dist/16/smile";
import Icon16Like from "@vkontakte/icons/dist/16/like";
import Icon16Dropdown from "@vkontakte/icons/dist/16/dropdown";

import { formatDate, APIManager, formatBalance } from "../helpers";

const { callMethod } = APIManager;

const Home = ({
  id,
  setPopout,
  setActivePanel,
  openPopoutSharing,
  openAlert,
  snackbar,
  openInfoVIP,
  openTransfer,
  setActiveModal,
  popout,
  openMenuVIP,
  activated,
  balance,
  accountType: at,
}) => {
  const [referrals, setReferrals] = useState(0);
  const [profit, setProfit] = useState([234]);
  const [referralsLocal, setReferralsLocal] = useState(130);
  const [accountType, setAccountType] = useState("Бронзовый");

  useEffect(() => {
    callMethod("getUserReferralsCount").then(({ message }) => {
      setReferrals(message);
    });
  }, []);

  function handleChangeSlider(referralsLocal) {
    switch (accountType) {
      case "Бронзовый":
        setProfit((referralsLocal * 9 * 0.2).toFixed(2));
        setReferralsLocal(referralsLocal);
        break;
      case "Серебряный":
        setProfit((referralsLocal * 9 * 0.3).toFixed(2));
        setReferralsLocal(referralsLocal);
        break;
      case "Золотой":
        setProfit((referralsLocal * 9 * 0.4).toFixed(2));
        setReferralsLocal(referralsLocal);
        break;
      case "Платиновый":
        setProfit((referralsLocal * 9 * 0.6).toFixed(2));
        setReferralsLocal(referralsLocal);
        break;
      default:
        break;
    }
  }

  function addGroup(fromUseEffect) {
    setActiveModal(null);
    console.log("add group");

    bridge
      .send("VKWebAppJoinGroup", { group_id: 196665515 })
      .then(({ result }) => {
        console.log("VKWebAppJoinGroup", result);
        if (result && fromUseEffect !== true) {
          openAlert("Вы уже подписаны на группу");
        }
      });
  }

  function AddToCommunity() {
    bridge.send("VKWebAppAddToCommunity", {});
  }

  function openActions() {
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem
            autoclose
            onClick={() => openPopoutSharing("stories")}
          >
            Позвать друзей
          </ActionSheetItem>
          {/* <ActionSheetItem autoclose onClick={openGeneralRules}>
            Правила
          </ActionSheetItem> */}
          <ActionSheetItem autoclose onClick={AddToCommunity}>
            Добавить в группу
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function openSelectAccount() {
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem
            autoclose
            onClick={() => {
              setAccountType("Бронзовый");
              setProfit((referralsLocal * 9 * 0.2).toFixed(2));
            }}
          >
            Бронзовый
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={() => {
              setAccountType("Серебряный");
              setProfit((referralsLocal * 9 * 0.3).toFixed(2));
            }}
          >
            Серебряный
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={() => {
              setAccountType("Золотой");
              setProfit((referralsLocal * 9 * 0.4).toFixed(2));
            }}
          >
            Золотой
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={() => {
              setAccountType("Платиновый");
              setProfit((referralsLocal * 9 * 0.6).toFixed(2));
            }}
          >
            Платиновый
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple>МММ</PanelHeaderSimple>

      <>
        <Div style={{ padding: "15px" }}>
          <Div
            // className="flexCenterColumn"
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-evenly",
              background: "#737373",
              height: "75px",
              borderRadius: "8px",
              margin: "0px 0px 10px 0px",
              padding: "0px 10px 0px 10px",
              color: "white",
            }}
          >
            {activated ? (
              <>
                <Div className="flexSpaceBetweenRow">
                  <Div>Аккаунт:</Div>
                  <Div>
                    {[, "Бронзовый", "Серебряный", "Золотой", "Платиновый"][at]}
                  </Div>
                </Div>
                <Div className="flexSpaceBetweenRow">
                  <Div></Div>
                  <Div>
                    <Button
                      style={{
                        backgroundColor: "green",
                        color: "white",
                      }}
                      mode="commerce"
                      size="m"
                      stretched
                      onClick={openMenuVIP}
                    >
                      Изменить
                    </Button>
                  </Div>
                </Div>
              </>
            ) : (
              <>
                <Div className="flexSpaceBetweenRow">
                  <Div>Аккаунт:</Div>
                  <Div>Не активирован</Div>
                </Div>
                <Div className="flexSpaceBetweenRow">
                  <Div></Div>
                  <Div>
                    <Button
                      style={{
                        backgroundColor: "green",
                        color: "white",
                      }}
                      mode="commerce"
                      size="m"
                      stretched
                      onClick={openMenuVIP}
                    >
                      Активировать
                    </Button>
                  </Div>
                </Div>
              </>
            )}
          </Div>

          <Div
            // className="flexCenterColumn"
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-evenly",
              background: "green",
              height: "75px",
              borderRadius: "8px",
              margin: "0px 0px 10px 0px",
              padding: "0px 10px 0px 10px",
              color: "white",
            }}
          >
            <Div className="flexSpaceBetweenRow">
              <Div>Баланс:</Div>
              <Div>{formatBalance(balance)}₽</Div>
            </Div>
            <Div className="flexSpaceBetweenRow">
              <Div></Div>
              <Div>
                <Button
                  style={{
                    backgroundColor: "#7a078c",
                    color: "white",
                  }}
                  mode="commerce"
                  size="m"
                  stretched
                  onClick={() => openTransfer("withdraw")}
                >
                  Вывести
                </Button>
              </Div>
            </Div>
          </Div>

          <Div
            // className="flexCenterColumn"
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-evenly",
              background: "#7a078c",
              height: "75px",
              borderRadius: "8px",
              margin: "0px 0px 10px 0px",
              padding: "0px 10px 0px 10px",
              color: "white",
            }}
          >
            <Div className="flexSpaceBetweenRow">
              <Div>Рефералов:</Div>
              <Div>{referrals}</Div>
            </Div>
            <Div className="flexSpaceBetweenRow">
              <Div></Div>
              <Div>
                <Button
                  style={{
                    backgroundColor: "#11729c",
                    color: "white",
                  }}
                  mode="commerce"
                  size="m"
                  stretched
                  onClick={openPopoutSharing}
                >
                  Пригласить
                </Button>
              </Div>
            </Div>
          </Div>

          <Div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-evenly",
              background: "#11729c",
              height: "140px",
              borderRadius: "8px",
              margin: "0px 0px 10px 0px",
              padding: "0px 10px 0px 10px",
              color: "white",
            }}
          >
            <Div className="flexSpaceBetweenRow">
              <Div>Ваш аккаунт:</Div>

              <Div>
                <Button
                  style={{
                    backgroundColor: "#737373",
                    color: "white",
                  }}
                  after={<Icon16Dropdown fill="white" />}
                  mode="commerce"
                  size="m"
                  stretched
                  onClick={openSelectAccount}
                >
                  {accountType}
                </Button>
              </Div>
            </Div>
            <Div className="flexSpaceBetweenRow">
              <Div>Расчет прибыли:</Div>
              <Div>{profit} ₽</Div>
            </Div>
            <Div className="flexSpaceBetweenRow">
              <Div>Рефералов:</Div>
              <Div>{referralsLocal} </Div>
            </Div>

            <Slider
              min={1}
              max={500}
              step={1}
              onChange={(referralsLocal) => handleChangeSlider(referralsLocal)}
              defaultValue={referralsLocal}
              // top="Минимальная прибыль"
              // bottom={`${profit}`}
            />
          </Div>
        </Div>
      </>
      {snackbar}
    </Panel>
  );
};

export { Home };
