import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  PullToRefresh,
  Button,
} from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";

import { TopUsersItem, TopGroupsItem } from "../items";
import {
  formatDate,
  APIManager
} from "../helpers";

const { callMethod } = APIManager;

const Top = ({
  id,
  go,
  vk,
  snackbar,
  openAlert,
  scheme,
  openMenuVIP,
  activated
}) => {
  const [userToken, setUserToken] = useState(null);
  const [activeTab, setActiveTab] = useState("general");
  const [friendsTop, setFriendsTop] = useState(null);
  const [renderFriends, setRenderFriends] = useState([]);
  const [fetching, setFetching] = useState(false);
  const [renderGlobal, setRenderGlobal] = useState(0);
  const [renderGroups, setRenderGroups] = useState(0);
  const [sliceItemUsers, setSliceItemUsers] = useState(50);
  const [sliceItemGroups, setSliceItemGroups] = useState(25);

  let groupsTop = [];
  let targetIdGroup = 0;

  useEffect(() => {
    getUsers();
    getGroups();
  }, []);

  function addGroupTop(e) {
    targetIdGroup = e.currentTarget.id;
    let urlGroup = groupsTop[targetIdGroup - 1].id;
    console.log("urlGroup", urlGroup);
    bridge
      .sendPromise("VKWebAppJoinGroup", {
        group_id: urlGroup,
      })
      .then((res) => {
        if (res.result) {
          openAlert("Вы уже подписаны на эту группу", "green");
        }

        console.log("res", res.result);
      })
      .catch((err) => {
        if (err.error_data.error_reason === "User can not join this group") {
          openAlert("Подайте заявку, чтобы вступить в группу", "red");
        }
        console.log("err", err);
      });
  }

  function tabGeneral() {
    setActiveTab("general");
    window.scrollTo(0, 0);
  }

  function tabFriends() {
    setActiveTab("friends");
    getFriendsAccess();
    window.scrollTo(0, 0);
  }

  function tabGroups() {
    setActiveTab("groups");
    window.scrollTo(0, 0);
  }

  function getUsers() {
    callMethod("getUsersTop").then(({ status, message }) => {
      if (status === "success") {
        console.log("getUsersTop", message);
        setRenderGlobal(
          message.map((item, index) => {
            return (
              <TopUsersItem
                key={index}
                id={index + 1}
                idVk={item.id}
                vipNameColor={item.vipNameColor}
                photoURL={item.photoURL}
                firstName={item.firstName}
                lastName={item.lastName}
                amount={item.pornCoins}
                scheme={scheme}
                openMenuVIP={openMenuVIP}
                activated={activated}
              />
            );
          })
        );
      }
      setFetching(false);
    });
  }

  function getGroups() {
    callMethod("getGroupsTop").then(({ status, message }) => {
      // console.log({ message, status });
      if (status === "success") {
        groupsTop = message;
        // console.log("setGroupsTop", groupsTop);
        setRenderGroups(
          message.map((item, index) => {
            return (
              <TopGroupsItem
                key={index}
                id={index + 1}
                idVk={item.id}
                photoURL={item.photoURL}
                name={item.name}
                addGroupTop={addGroupTop}
                scheme={scheme}
                activated={activated}
              />
            );
          })
        );
      }

      setFetching(false);
    });
  }

  function onRefresh() {
    // ВЫЗВАТЬ ЗАПРОС ДАННЫХ ДЛЯ ОТДЕЛЬНОГО ТОПА , ОТОШЕЛ НАЛИТЬ ЧАЯ
    setFetching(true);
    switch (activeTab) {
      case "general":
        getUsers();
        break;
      case "friends":
        getFriendsAccess();
        break;
      case "groups":
        getGroups();
        break;
      default:
        break;
    }
  }

  function getFriendsAccess() {
    bridge
      .sendPromise("VKWebAppGetAuthToken", {
        scope: "friends",
        app_id: 7523466,
      })
      .then((res) => {
        if (res.access_token) {
          setUserToken(res.access_token);
          getAndSendToServerFriends(res.access_token);
        } else {
          // TODO: Сделать ошибку о том, что пользователь не предоставил доступ к друзьям
        }
      })
      .catch((err) => {
        // vk.log(err);
        console.log(err);
      });
  }

  function getAndSendToServerFriends(userToken) {
    bridge
      .sendPromise("VKWebAppCallAPIMethod", {
        method: "friends.get",
        params: {
          access_token: userToken,
          v: "5.103",
        },
      })
      .then((res) => {
        if (res.response) {
          callMethod("getFriendsTop", {
            items: res.response.items,
          }).then(({ message }) => {
            if (!message.length) {
              return setFriendsTop("У Вас нет друзей в игре");
            }
            console.log(message);
            setFriendsTop(true);
            setFetching(false);
            setRenderFriends(
              message.map((item, index) => {
                return (
                  <TopUsersItem
                    key={index}
                    id={index + 1}
                    idVk={item.id}
                    photoURL={item.photoURL}
                    vipNameColor={item.vipNameColor}
                    firstName={item.firstName}
                    lastName={item.lastName}
                    amount={item.pornCoins}
                    scheme={scheme}
                    openMenuVIP={openMenuVIP}
                    activated={activated}
                  />
                );
              })
            );
          });
        } else {
          return setFriendsTop("Ошибка при загрузке друзей");
        }
      })
      .catch((err) => {
        return setFriendsTop("Ошибка при загрузке друзей");
      });
  }

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple left={<PanelHeaderBack onClick={go} data-to="home" />}>
        Топ
      </PanelHeaderSimple>

      <>
        <FixedLayout vertical="top" filled={true}>
          <Tabs>
            <TabsItem onClick={tabGeneral} selected={activeTab === "general"}>
              Общий
            </TabsItem>
            <TabsItem onClick={tabFriends} selected={activeTab === "friends"}>
              Друзья
            </TabsItem>
            <TabsItem onClick={tabGroups} selected={activeTab === "groups"}>
              Сообщества
            </TabsItem>
          </Tabs>
        </FixedLayout>

        <>
          {activeTab === "general" ? (
            <>
              {renderGlobal.length ? (
                <>
                  <Div style={{
                    marginTop: "25px"
                  }}>
                    Ваше место: 222
                  </Div>
                  <PullToRefresh
                    onRefresh={onRefresh}
                    isFetching={fetching}
                    style={{ marginTop: 10 }}
                  >
                    <Div>{renderGlobal.slice(0, sliceItemUsers)}</Div>
                    {sliceItemUsers === 100 ? (
                      <></>
                    ) : (
                      <Div
                        style={{
                          marginTop: "15px",
                          marginBottom: "15px",
                          marginLeft: "10px",
                          marginRight: "10px",
                        }}
                      >
                        <Button
                          size="l"
                          stretched
                          onClick={() => setSliceItemUsers(sliceItemUsers + 25)}
                          style={{
                            backgroundColor: "green",
                            color: "white",
                          }}
                        >
                          Показать ещё
                        </Button>
                      </Div>
                    )}
                  </PullToRefresh>
                </>
              ) : (
                <div className="flexCenterColumn">
                  <Spinner size="large" style={{ marginTop: 90 }} />
                </div>
              )}
            </>
          ) : (
            ""
          )}

          {activeTab === "friends" ? (
            <div style={{ marginTop: 55 }}>
              {friendsTop === null ? (
                <div className="flexCenterColumn">
                  <Spinner size="large" style={{ marginTop: 90 }} />
                </div>
              ) : !renderFriends.length ? (
                <Div>{friendsTop}</Div>
              ) : (
                <PullToRefresh
                  onRefresh={onRefresh}
                  isFetching={fetching}
                  style={{ marginTop: 10 }}
                >
                  <Div>{renderFriends}</Div>
                </PullToRefresh>
              )}
            </div>
          ) : (
            ""
          )}

          {activeTab === "groups" ? (
            <div style={{ marginTop: 55 }}>
              {renderGroups.length ? (
                <PullToRefresh
                  onRefresh={onRefresh}
                  isFetching={fetching}
                  style={{ marginTop: 10 }}
                >
                  <Div>{renderGroups.slice(0, sliceItemGroups)}</Div>
                  {renderGroups.length > 25 ? (
                    <Div
                      style={{
                        marginTop: "15px",
                        marginBottom: "15px",
                        marginLeft: "10px",
                        marginRight: "10px",
                      }}
                    >
                      <Button
                        size="l"
                        stretched
                        onClick={() => setSliceItemGroups(sliceItemGroups + 25)}
                        style={{
                          backgroundColor: "green",
                          color: "white",
                        }}
                      >
                        Показать ещё
                      </Button>
                    </Div>
                  ) : (
                    <></>
                  )}
                </PullToRefresh>
              ) : (
                <div className="flexCenterColumn">
                  <Spinner size="large" style={{ marginTop: 90 }} />
                </div>
              )}
            </div>
          ) : (
            ""
          )}
        </>

        {snackbar}
      </>
    </Panel>
  );
};

export { Top };
